    const calculateSplits = (users, group) => {
        // Divide entries by the number of users covering the job, split
        let start = group === 0? 0 : (messagingList.length * group / users) + 1;
        let end = messagingList.length * (group+1) / users;
        return ({
            start: start,
            end: end
        });
    }

