export const MessageBox = (props) => {
    const {limitLength, setter, messageContents} = props;
    return (
        <div className = "MessageBox">
            <label for="message"> Message: </label>
            <textarea value = {messageContents} id="message" name="message" onChange = {(event) => setter(event.target.value)}/>
            <div className = "TextPreview">
                <label> Text Preview (Ignore if poll):</label>
                <textarea value= {messageContents} disabled={true} />
            </div>
        </div>
    );
}